import uniPopup from '@/components/uni-popup/uni-popup.vue'
import xselect from '../../wxcomponents/xselect/xselect.vue'
import request from '../../components/pocky-request/index.js'

export default {
	onLoad(sid) {
		this.requestUrl()
		uni.request({
			url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandDetail',
			method: 'POST',
			data: {
				id:1
			},
			success: res => {
				this.phoneType = res.data.data
			}
		})

		// console.log(sid)
		
		if(sid.id){
			this.quickId = sid.id
		}else if(sid.model){
			let smodel = sid.model
			uni.setStorage({
				key:'model',
				data:smodel,
				success:res=>{
					uni.request({
						url:'https://www.finetwm.com/xmRepair/phoneBrand/getBrandColour',
						data:{
							name:smodel
						},
						method:'POST',
						success:res=>{
							this.popupContent = res.data.data
							this.$refs.popup.open()
						}
					})
				}
			})
			console.log('本机维修')
		}else{
			return
		}
		
	},
	data() {
		return {
			quickId:'',
			valueId:1,
			colorId:null,
			selectedId: 0,
			popupContent: [{
					name: "银色",
					value: "#E7E3E2"
				},{
					name: "金色",
					value: "#DAC8B4"
				},{
					name: "深空灰",
					value: "#2A2526"
				}],
			isSelected: false,
			phone: '手机',
			pad: '平板',
			slideList: [],
			phoneType: []
		}
	},
	methods: {
		onNext() {
			if(this.colorId !== null){
				uni.navigateTo({
					url: '../faults/faults?id=' + this.quickId
				})
			}
		},
		openPopup(e) {
			let modelSelected = this.phoneType[e].model
			uni.setStorage({
				key:'model',
				data:modelSelected
			})
			this.$refs.popup.open()
			
			//获取颜色色值
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandColour',
				method: 'POST',
				data: {
					name: modelSelected
				},
				success: res => {
					this.popupContent = res.data.data
					// uni.setStorage({
					// 	key:'color',
					// 	data:svalue
					// })
				}
			})
		},
		closePopup() {
			this.$refs.popup.close()
			this.colorId = null
		},
		onSelectedId(e) {
			this.selectedId = e.currentTarget.id
			this.valueId = parseInt(e.currentTarget.id) + 1
			let svalueId = e.currentTarget.id
			let svalue = this.slideList[svalueId].brand
			uni.setStorage({
				key:'brand',
				data:svalue
			})
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandDetail',
				method: 'POST',
				data: {
					id:this.valueId
				},
				success: res => {
					this.phoneType = res.data.data
				}
			})
		},
		onSelectedColor(e){
			this.colorId = e.currentTarget.id
			// console.log(this.colorId)
			let selectedColorName = this.popupContent[this.colorId].name
			let selectedColorValue = this.popupContent[this.colorId].value
			uni.setStorage({
				key:'colorName',
				data:selectedColorName
			})
			uni.setStorage({
				key:'colorValue',
				data:selectedColorValue
			})
		},
		
		requestUrl(){
			const instance = new request()
			const requestModel = instance.get({
				url:'/phoneBrand/getBrand',
				contentType: 'json',
			}).then(res=>{
				this.slideList = res.data.data
				console.log(res)
			}).catch(err=>{
				console.log(err)
			})
		}

	},
	components: {
		uniPopup,
		xselect
	}
}
